import React from 'react'
import PropTypes from 'prop-types'

// Mui
import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'

// Core
import Card from './components/Card'

import { useStyles } from './styles'

function Users(props) {
  const classes = useStyles()

  const { list } = props

  return (
    <div className={classes.root}>
      <Typography component="h1">Users</Typography>

      {
        list.map((org, idx) => (
          <div key={idx} style={{ marginBottom: '2em' }}>
            <img alt={org.name} src={org.logo} width="32" height="30" style={{ marginRight: 20 }} />
            <Typography
              component="h2"
              style={{
                fontSize: 24, color: '#171725', marginBottom: 20, display: 'inline-block'
              }}
            >
              {org.name}
            </Typography>
            <Grid container spacing={3}>
              {
            org.users.map(item => (
              <Grid key={item.id} item xs={12} sm={4} md={3}>
                <Card data={item} orgName={org.name} />
              </Grid>
            ))
          }
            </Grid>
          </div>
        ))
      }
    </div>
  )
}

Users.propTypes = {
  list: PropTypes.array
}

export default Users
